@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
:root {
  --default-blue: #294cca;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.background {
  width: 100vw;
  height: 100vh;
  display: flex;
}

.backgroundSmooth {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.884);
}

body {
  overflow-x: hidden;
}
::-webkit-scrollbar {
  border: 2px solid #294cca;
  border: 2px solid var(--default-blue);
  display: flex;
  width: 10px;
}

::-webkit-scrollbar-thumb {
  background: #294cca;
  background: var(--default-blue);
  width: 20px;
  border-radius: 20px;
}

::-webkit-scrollbar-track {
  background: rgb(192, 192, 192);
}

.navbar button {
  border: none;
}

.navbar button:focus {
  border: none;
  outline: none;
}

.bg {
  background-color: rgba(0, 0, 0, 0);
}

.userLogo {
  position: absolute;
  right: 150px;
  align-items: center;
  transition: 0.8s;
}
.userLogo:hover {
  opacity: 70%;
}

#navItem {
  font: 700 1rem 'Roboto';
  color: rgb(99, 99, 99);
}

#navItem:hover {
  background-color: rgb(235, 235, 235);
  color: #000;
}
#navItem:focus {
  background-color: rgba(255, 255, 255);
}

.nav-item span {
  color: white;
  margin-left: 40px;
  transition: 0.1s;
}
.nav-item span:hover {
  border: 2px solid white;
  border-top: none;
  border-left: none;
  border-right: none;
  color: rgb(226, 226, 226);
}

.navbar {
  font: 700 1rem Roboto;
}

.dropdown-menu {
  border: 0px solid rgba(0, 0, 0, 0.15) !important;
  background-color: #fafafa;
}

@media (max-width: 991px) {
  .userLogo {
    right: 50px;
    top: 80px;
  }
}
@media (min-width: 992px) {
  .dropdown .dropdown-menu {
    transition: all 0.5s;
    overflow: hidden;
    transform-origin: top center;
    transform: scale(1, 0);
    display: block;
  }
  .dropdown:hover .dropdown-menu {
    transform: scale(1);
  }
  .dropdown .dropdown-menu .dropdown-item {
    font-size: 14px;
    padding: 9px 18px !important;
    color: #000 !important;
    text-transform: capitalize;
  }
}

